.Message {
  opacity: 0;
  transition: opacity ease-in-out 500ms;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.Message-fadein {
  opacity: 1;
}

.date-line {
  text-align: center;
  font-size: 0.75rem;
}

.date-line > hr {
  border-color: white;
}

.message-timestamp {
  font-size: 0.75rem;
}

.message-header {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.message-author {
  font-size: 1.15rem;
}

.message-avatar {
  min-width: 45px;
  height: inherit;
  position: relative;
}

.message-avatar > img {
  width: 41px;
  height: auto;
  border-radius: 50%;
  position: absolute;
}

.hide {
  display: none;
}

.pad-top {
  padding-top: 1rem;
}

.message-container {
  display: flex;
  gap: 0.5rem;
}

.message-content {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.content-container {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.content-container > i {
  font-size: 0.8rem;
}

.content-link {
  text-decoration: none;
  color: dodgerblue;
  word-wrap: anywhere;
}

.embed-video {
  flex: 1;
  /* max-width: max-content; */
  padding: 0.2rem;
}

video {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.attachment {
  flex: 1;
  padding: 0.2rem;
}

.attachment > img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}
